<template>
	<div id="it-sidebar" class="uk-width-1-5@m uk-width-1-6@xl uk-background-secondary uk-height-viewport@m">
		
		<div id="it-mobile-navigation" class="uk-hidden@m">
			
			<div uk-sticky="sel-target: .uk-navbar-container; cls-active: uk-navbar-sticky">
				<nav id="it-mobile-navigation-top" class="uk-navbar-container" uk-navbar>
					<div class="uk-navbar-center uk-light">
						<Logo />
					</div>
					<div class="uk-navbar-right">
						<a class="uk-navbar-toggle" uk-toggle="target: #offcanvas-nav" uk-icon="icon: user; ratio: 1.5"></a>
					</div>
				</nav>
			</div>

			<div id="it-mobile-navigation-bottom" class="uk-position-bottom uk-position-fixed">
				<ul class="uk-iconnav uk-flex-center">
					<li>
						<router-link to="/" uk-icon="icon: home; ratio: 1.4"></router-link>
					</li>
					<li>
						<router-link to="/series" uk-icon="icon: grid; ratio: 1.4"></router-link>
					</li>
					<li>
						<router-link to="/authors" uk-icon="icon: pencil; ratio: 1.4"></router-link>
					</li>
					<li v-if="username">
						<router-link :to="{ name: 'profile' }" uk-icon="icon: heart; ratio: 1.4"></router-link>
					</li>
					<li v-else>
						<a href="#modal-login" uk-toggle uk-icon="icon: heart; ratio: 1.4"></a>
					</li>
				</ul>
			</div>
		</div>

		<aside class="uk-visible@m uk-light">
			<div class="uk-inline uk-height-viewport uk-width-1-1">
				<div id="sidebar-brand" class="uk-padding-small uk-margin-top uk-margin-medium-bottom">
					<Logo />
				</div>

				<div id="sidebar-user" class="uk-margin-medium-bottom uk-padding uk-padding-remove-vertical">
					<!-- If you are logged in -->
					<div v-if="username">
						<h4 class="uk-margin-remove">{{ username }}</h4>
						<p class="uk-margin-remove-top">
							<router-link :to="{ name: 'profile' }">
								Mi perfil
							</router-link> - 
							<a @click="logout">{{'LOGOUT' | translate}}</a>
						</p>
						<!-- <router-link :to="{ name: 'admin' }">
							Admin
						</router-link> -->
					</div>

					<!-- If you are not logged in -->
					<div v-else>
						<a href="#modal-login" uk-toggle class="uk-h4">
							{{'LOGIN_TITLE' | translate}}
						</a>
					</div>
				</div>

				<nav id="sidebar-navigation" class="uk-margin-medium-bottom uk-padding uk-padding-remove-vertical">
					<!-- <MenuDefault v-if="admin"/>
						<MenuAdmin v-else /> -->

						<ul class="uk-nav uk-nav-default">
							<li>
								<router-link :to="{name: 'home'}">{{'HOME' | translate}}</router-link>
							</li>
							<li>
								<router-link to="/series">{{'SERIES' | translate}}</router-link>
							</li>
							<li>
								<router-link to="/authors">{{'AUTHOR_ALL' | translate}}</router-link>
							</li>
							<!-- <li>
								<router-link to="/shop">{{'SHOP' | translate}}</router-link>
							</li> -->
							<li>
								<a href="#modal-full" uk-toggle @click="initSearch">Buscar</a>
							</li>
						</ul>
					</nav>

					<Footer />				

					<div id="modal-full" class="uk-modal" uk-modal>
						<div class="uk-modal-dialog uk-margin-auto-vertical uk-flex uk-flex-center uk-flex-middle uk-height-medium">
							<button class="uk-modal-close-full" type="button" uk-close ref="email"></button>
							<form id="it-search" class="uk-search uk-search-large" v-on:submit.prevent="search">
								<input
								class="uk-search-input uk-text-center"
								type="search"
								v-model="searchText"
								:placeholder="'SEARCH_PLACEHOLDER' | translate"
								autofocus
								/>
							</form>
						</div>
					</div>

					<Login></Login>
				</div>
			</aside>


			<Offcanvas></Offcanvas>
		</div>

	</template>

	<script>
		import {NAV_GENRE_ALL_GENRES}  from '../constants/graphql';
		import Logo from "./Logo.vue";
		//import MenuDefault from "./Menu/MenuDefault.vue";
		//import MenuAdmin from "./Menu/MenuAdmin.vue";
		import Footer from "./Footer.vue";
		import Offcanvas from "./Offcanvas.vue";
		import Login from "../containers/users/Login.vue";
		import { mapMutations, mapGetters } from "vuex";
		import languages from "../assets/localization/languages";
		import strapi from "../../utils/Strapi";

		export default {
			name: "Nav",
			mounted: function () {
				this.loadUsername();
			},
			methods: {
				...mapMutations("i18n", ["setCurrentLanguage"]),
				initSearch() {
					this.searchText = "";
				},
				search() {
					this.$refs.email.click();
					this.$router.push("/search?q=" + this.searchText);
				},
				logout() {
					this.$store.dispatch("auth/logout");
					this.loadUsername();
					this.$router.go();
				},
				loadUsername() {
					setTimeout(() => {
						this.username = this.$store.getters["auth/getUsername"];
						this.userId = this.$store.getters["auth/getId"];
					}, 100);
				},
				async saveUserPreference(preferenceLanguage) {
					await strapi.updateEntry("users", this.userId, {
						language: preferenceLanguage,
					});
				},
			},
			components: {
				Offcanvas,
				Logo,
				Footer,
				Login,
				//MenuDefault,
				//MenuAdmin
			},
			computed: {
				admin(){
					return this.$route.name != 'admin';
				},
				showGenre() {
					return this.$route.name != "serie" && this.$route.name != "profile";
				},
				...mapGetters("i18n", ["currentLanguage"]),
				selectedLanguage: {
					get() {
						return this.currentLanguage;
					},
					set(value) {
						/* save user preference language */
						this.saveUserPreference(value);
						this.setCurrentLanguage(value);
					},
				},
			},
			data() {
				return {
				//categories: [],
				username: null,
				userId: null,
				availableLanguages: languages,
				searchText: this.$route.query.q,
			};
		},
		apollo: {
			//categories: NAV_ALL_CATEGORIES,
			genres: NAV_GENRE_ALL_GENRES
		},
	};
</script>